<template>
    <div>     
        <v-skeleton-loader 
            v-if="isFetchingUserData"
            type="heading, table-tbody" 
            class="skeleton mx-auto" 
            max-width="500" 
            max-height="400" 
        />
        <v-container v-else class="container" fluid>
            <v-text-field
            v-model.trim="searchKeyword"
            solo
            label="Search"
            placeholder="Search"
            @keyup.enter="addSearchKeyword(searchKeyword)"
            class="container__searchbox"
            maxlength="50"
            />
            <v-row class="container__chips" v-if="showSearchedKeywords">
                <div v-for="(keyword, index) in userDetailsStore.getKeywords"
                :key="index"
                color="#FFCC80"
                class="container__chips__chip"
                @click="userDetailsStore.removeSearchedKeyword(keyword)"
                >
                    {{ keyword }}
                    <v-icon 
                    right 
                    size="22"
                    class="container__chips__chip__icon"
                    @click.stop="userDetailsStore.removeSearchedKeyword(keyword)"
                    >
                    mdi-close
                    </v-icon>
                </div>
            </v-row>
            <table class="table" v-if="filteredUserData?.length">
                <thead class="table__head">
                <th class="table__head__data">S.No</th>
                <th class="table__head__data">Name</th>
                <th class="table__head__data">see user data</th>
                </thead>
                <tbody class="table__body">
                    <tr v-for="(user, key) in filteredUserData" :key="user.id" class="table__body__row">
                        <td class="body__row__data">{{ key + 1 }}</td>                       
                        <td class="body__row__data" v-if="showSearchedKeywords">
                            <span v-for="(words, index) in highlightSearchedKeyword(user.name)"
                            :key="index"
                            :class="words.highlight ? 'body__row__data--selected' : ''"
                            >{{ words.name }}</span>
                        </td>
                        <td class="body__row__data" v-else>
                            {{ user.name }}
                        </td>
                        <td class="body__row__data">
                            <button class="body__row__data__button" @click="setUserData(user.id)">Check Details</button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <h1 v-else>No Results Found</h1>
        </v-container>
    </div>
</template>

<script setup lang="ts">
import axios from 'axios'
import { computed, onMounted, ref } from 'vue'

import { User } from '@/types/Type'
import router from '@/router/index'
import { useUserDetailsStore } from '@/store'

const userDetailsStore = useUserDetailsStore()
const isFetchingUserData = ref(true)
const searchKeyword = ref<string>('')
const showSearchedKeywords = ref<boolean>(true)
const SHOW_LOADING_TIMEOUT_IN_MS = 300

const setUserData = (userId:number) => {
    router.push({
        name: 'userDetails',
        params: {id: `${userId}`}
    })
}

const lastSearchedKeyword = computed(() => userDetailsStore.getLastKeyword)

const filteredUserData = computed(() => {
    const key = lastSearchedKeyword.value
    
    return userDetailsStore.getUserData?.filter((user) => user.name.toLowerCase().includes(key))
})

const addSearchKeyword = (keyword:string) => {
    const isKeywordAdded = keyword !== '' && userDetailsStore.addSearchedKeyword(keyword) === 'fail'
    const isKeywordIncludes = !userDetailsStore.getKeywords.includes(keyword)
    
    if(isKeywordAdded && isKeywordIncludes) {
        userDetailsStore.removeSearchedKeyword(keyword)
        userDetailsStore.addSearchedKeyword(keyword)
    }
}

const highlightSearchedKeyword = (name: string) => {
    const keyword = lastSearchedKeyword.value
    const startIndex = name.toLowerCase().lastIndexOf(keyword.toLowerCase())
    let splittedWords = []
    splittedWords.push({name: `${name.slice(0, startIndex)}`, highlight: false})
    splittedWords.push({name: `${name.slice(startIndex, startIndex + keyword.length)}`, highlight: true})
    splittedWords.push({name: `${name.slice(startIndex + keyword.length)}`, highlight: false})

    return splittedWords
}

onMounted(async () => {
    const response = await axios({
        method: 'get',
        url: 'https://jsonplaceholder.typicode.com/users'
    })   
    userDetailsStore.setUsersData(response.data)
    const loadingTime = setTimeout(() => {
        isFetchingUserData.value = false
        clearTimeout(loadingTime)
    }, SHOW_LOADING_TIMEOUT_IN_MS)
})
</script>

